import React, { useState } from 'react';
import './styles.css';
import { Form, Select, DatePicker, Input, Button } from 'antd';
import { Col, Popconfirm, message } from "antd";
import TextArea from 'antd/lib/input/TextArea';
import { hideModal } from '../../../actions/index';
import { connect } from 'react-redux';

const CreateTaskEmployee = ({ dispatch, remainWeight }) => {
  const { Option } = Select;
  const [selectedTypeTask, setSelectedTypeTask] = useState();
  const [messageCheckDevice, setMessageCheckDevice] = useState(
    'C y  thit b khng? Cc thit b cn : M s thit b, tem'
  );
  const [messageCheckVeSinh, setMessageCheckVeSinh] = useState([
    'Kim tra trang phc: Qun o, m, gang tay, giy dp',
    'Kim tra v sinh khu vc sn xut',
    'Kim tra cht st trng v ty ra'
  ]);

  const [messageCheckProduction, setMessageCheckProduction] = useState(
    'Kim tra vic p dng quy trnh sn xut'
  );

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 5 }
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 }
    }
  };
  const children = [];

  children.push(<Option key='V Duy Mnh'>DBCL_01 - V Duy Mnh</Option>);
  children.push(<Option key='L nh Phc'>DBCL_02 - L nh Phc</Option>);
  children.push(<Option key='V Vn Nam'>DBCL_03 - V Vn Nam</Option>);
  children.push(<Option key='L Minh Nguyn'>DBCL_04 - L Minh Nguyn</Option>);

  function handleChange(value) {
    console.log(`selected ${value}`);
  }

  function handleChangeSelect(value) {
    setSelectedTypeTask(value);
  }

  function handleChangeCheckDevice(value) {
    setMessageCheckDevice(value.target.value);
  }

  function handleChangeCheckVeSinh(value) {
    setMessageCheckVeSinh(value.target.value);
  }

  function handleChangeCheckProduction(value) {
    setMessageCheckProduction(value.target.value);
  }
  function confirm() {
    message.success('Bn  to cng vic thnh cng !');
    dispatch(hideModal())
  }
  return (
    <div className='create-task-employee'>
      <Form {...formItemLayout}>
        <Col xl={24} lg={24} style={{ margin: '0 auto' }}>
          <Form.Item label='Tn cng vic'>
            <Input placeholder='' />
          </Form.Item>
          <Form.Item label='Ngi giao vic'>
            <Input placeholder='' disabled={true} value='V Duy Mnh' />
          </Form.Item>
          <Form.Item label='Chc v'>
            <Input placeholder='' disabled={true} value='Trng phng b phn m bo cht lng' />
          </Form.Item>
          <Form.Item label={`Trng s (cn li ${remainWeight})`}>
            <Input placeholder={`0.0 <= Trng s  <= ${remainWeight}`} />
          </Form.Item>
          <Form.Item label='Loi cng'>
            <Select
              style={{ width: '100%' }}
              placeholder='Chn giai on kim tra'
              onChange={handleChange}
            >
              <Option key='1'>Kim tra nguyn liu u vo</Option>
              <Option key='2'>Kim tra quy trnh sn xut</Option>
              <Option key='3'>m bo cht lng u ra</Option>
            </Select>
          </Form.Item>

          <Form.Item label='Ngi kim duyt'>
            <Select
              mode='tags'
              style={{ width: '100%' }}
              placeholder='Chn ngi kim duyt'
              onChange={handleChange}
            >
              {children}
            </Select>
          </Form.Item>
          <Form.Item label='Loi cng vic'>
            <Select defaultValue='0' onChange={handleChangeSelect}>
              <Option value='0'>Chn loi cng vic</Option>
              <Option value='1'>
                Kim tra cht lng quy trnh xng thuc nc th y
              </Option>
              <Option value='2'>Kim tra cht lng bao b cp 1</Option>
              <Option value='3'>
                Kim tra cht lng quy trnh xng thuc bt th y
              </Option>
              <Option value='4'>
                Kim tra cht lng quy trnh xng thc phm chc nng
              </Option>

              <Option value='5'>Kim tra cht lng bao b cp 2</Option>
              <Option value='6'>Kim tra cht lng nguyn liu</Option>
            </Select>
          </Form.Item>

          {selectedTypeTask === '1' ? (
            <>
              <Form.Item label='S ln kim tra nh k 1 thng'>
                <Input placeholder='' />
              </Form.Item>
              <Form.Item label='S ln kim tra t xut 1 thng'>
                <Input placeholder='' />
              </Form.Item>
              <Form.Item label='Kim tra thit b sn xut'>
                <TextArea
                  rows={4}
                  value={messageCheckDevice}
                  onChange={handleChangeCheckDevice}
                ></TextArea>
              </Form.Item>
              <Form.Item label='Kim tra v sinh'>
                <TextArea
                  rows={4}
                  value={messageCheckVeSinh.map((item, id) => {
                    if (id === 0) {
                      return item;
                    }
                    return '\n' + item;
                  })}
                  onChange={handleChangeCheckVeSinh}
                ></TextArea>
              </Form.Item>
              <Form.Item label='Kim tra quy trnh sn xut'>
                <TextArea
                  rows={4}
                  value={messageCheckProduction}
                  onChange={handleChangeCheckProduction}
                ></TextArea>
              </Form.Item>
            </>
          ) : null}
          {selectedTypeTask === '2' ? (
            <>
              <Form.Item label='S bao b sn phm cn kim tra'>
                <Input placeholder='' />
              </Form.Item>
            </>
          ) : null}
          <Form.Item label='Deadline' style={{ marginBottom: 0 }}>
            <Form.Item
              style={{ display: 'inline-block', width: 'calc(50% - 12px)' }}
            >
              <DatePicker />
            </Form.Item>
          </Form.Item>
        </Col>
        <Col xl={24} lg={24} style={{ float: 'right' }}>
          <Popconfirm placement="top" title={"Bn c chc mun to mi cng vic khng ?"} onConfirm={confirm} okText="Yes" cancelText="No">
            <Button
              type='primary'
              style={{ display: 'block', marginRight: '180px', float: "right" }}
              htmlType='submit'

            >
              To mi
        </Button>
          </Popconfirm>
          <Button
            type='danger'
            style={{ float: 'right', marginRight: '10px' }}
            onClick={() => dispatch(hideModal())}
          >
            Hu
        </Button>
        </Col>
      </Form>
    </div >
  );
};

export default connect()(CreateTaskEmployee);
